import React, { useState, useEffect } from 'react';
import LoggedNavBar from './LoggedNavBar';
import * as Bootstrap from 'react-bootstrap';
import '../bootstrap/dist/css/bootstrap.css';
import LoggedRouter from './LoggedRouter';
import { Link } from 'react-router-dom';
import '../App.css';
import SideComponent from './SideComponent';
import axios from 'axios';
import LoadingComponent from './LoadingComponent';
import REACTDOM from 'react-dom';

function LoggedHome(props) {
    const [hasSavedNotes, setHasSavedNotes] = useState(false);
    const [isLoading, setIsLoading] = useState(true);
    const [data, setData] = useState();
    var dataList;

    const username = props.location.username;

    const arrayData = [
        {
            0: { _id: "5fb699e12f5500416461f9a3", url: "jsdk", username: "joshuafosu119@gmail.com", password: "isd" },
            1: { _id: "5fb6a827ce32773b04e7ba0b", url: "jsdk", username: "joshuafosu119@gmail.com", password: "ew" },
            2: { _id: "5fb6a921825f241a00ad4af7", url: "jsdk", username: "j", password: "kwe" }
        }
    ]

    function RenderRows() {
        console.log("This is the array dat");
        console.log(arrayData);
        dataList = arrayData.map(darray => (
            <div key={darray[0]?._id}>
                <p>{darray[0][0]?._id}</p>
                <p>{darray[0][0]?._id}</p>
            </div>
        )
        )
        return dataList;
    }

    function SetRows() {
        dataList = arrayData.map(darray => (
            <div key={darray[0]?._id}>
                <p>{darray[0]?._id}</p>
                <p>{darray[0]?.username}</p>
            </div>
        )
        )
        return dataList;
    }

    useEffect(() => {
        console.log("Nlksd");
        axios.get('/api/getNotes/' + username).then(res => {
            setIsLoading(false);
            if (res.length === 0) {
                setHasSavedNotes(false);
            }
            else {
                setHasSavedNotes(true);
                setData(res.data);
                console.log(res.data);
            }

        }).catch(err => {
            RenderRows();
            /*setIsLoading(false);*/
            <Bootstrap.Alert variant="primary">
                <Bootstrap.Alert.Heading>We could't get your password savers</Bootstrap.Alert.Heading>
            </Bootstrap.Alert>
        })
    })






    return (
        <>
            <div>
                <LoggedNavBar>
                    {username}
                </LoggedNavBar>
            </div>
            <div className="Container_page">
                <div className="Left_page bg-dark">
                    <SideComponent>
                        {username}
                    </SideComponent>
                </div>
                <div className="Right_page">
                    <Bootstrap.Nav justify variant="tabs" defaultActiveKey="/home">
                        <Bootstrap.Nav.Item>
                            <Bootstrap.Nav.Link href="#">Notes</Bootstrap.Nav.Link>
                        </Bootstrap.Nav.Item>
                        <Bootstrap.Nav.Item>
                            <Bootstrap.Nav.Link href="#about">About Us</Bootstrap.Nav.Link>
                        </Bootstrap.Nav.Item>
                        <Bootstrap.Nav.Item>
                            <Bootstrap.Nav.Link href="#history">
                                History
                        </Bootstrap.Nav.Link>
                        </Bootstrap.Nav.Item>
                        <Bootstrap.Nav.Item>
                            <Bootstrap.Nav.Link href="#profile">
                                Profile
                    </Bootstrap.Nav.Link>
                        </Bootstrap.Nav.Item>
                    </Bootstrap.Nav>

                    <div>
                        <div>

                            <p>
                                {
                                    data.forEach(fr => {
                                        console.log("jsodos htp iofdsdis");
                                        console.log(fr?.username);
                                    })
                                } 
                            </p>

                            <p>To come after it all</p>
                        </div>
                        {
                            isLoading === true ?
                                <div>


                                </div>
                                /*
                                    <div className="ml-auto" style={{ marginTop: "150px" }}>
                                        <LoadingComponent />
                                    </div>
                                */
                                :
                                <div>

                                    {
                                        hasSavedNotes === false ?

                                            <p className="text-muted">
                                                No Stored Password saver found
                                        </p>
                                            :
                                            <div>
                                                {
                                                    arrayData.map()
                                                }

                                            </div>


                                    }

                                </div>
                        }
                    </div>
                </div>



            </div>
            <div>

                THis is where the footer will be placed
                ksldkwpelk
            </div>

        </>
    )
}


export default LoggedHome;



